import Cryptojs from 'crypto-js'
export function getToken () {
  return sessionStorage.u_id || ''
}
export function objKeySort (arys) {
  var newkey = Object.keys(arys).sort()
  var newObj = {}
  for (var i = 0; i < newkey.length; i++) {
    newObj[newkey[i]] = arys[newkey[i]]
  }
  return newObj
}
export function sgin (arr) {
  var str = ''
  var hash = ''
  var key = 'MDc2OWJkYWI0ZGJiMmMxMzBjNzA3MGQ5NTU0MDVkODE='
  for (var index in arr) {
    str += index + '=' + arr[index] + '&'
  }
  var strsgin = str.substring(0, str.length - 1)
  var newstr = strsgin + key
  hash = Cryptojs.HmacSHA256(newstr, key).toString()
  return hash
}
export function setToken (token) {
  sessionStorage.setItem('token', token)
}

export function getUser () {
  const userInfo = sessionStorage.user
  if (userInfo) {
    return JSON.parse(userInfo)
  } else {
    return {}
  }
}

export function setUser (userInfo) {
  sessionStorage.setItem('user', JSON.stringify(userInfo))
}

export function add0 (m) { return m < 10 ? '0' + m : m }
export function format (isTime) {
  var times = isTime * 1000
  var time = new Date(times)
  var y = time.getFullYear()
  var m = time.getMonth() + 1
  var d = time.getDate()
  // var h = time.getHours()
  // var mm = time.getMinutes()
  // var s = time.getSeconds()
  // console.log(y + '-' + this.add0(m) + '-' + this.add0(d) + ' ' + this.add0(h) + ':' + this.add0(mm) + ':' + this.add0(s))
  return y + '-' + add0(m) + '-' + add0(d)
}
