<template>
  <div id="app">
    <!-- v-show="this.$route.meta.show" -->
      <van-tabbar v-model="active" v-show="this.$route.meta.show">
        <van-tabbar-item @click="toHome()">
          <span>{{$t('common.shouye')}}</span>
          <template #icon="props">
            <img :src="props.active ? require('./assets/image/home1.png') : require('./assets/image/home2.png')" />
          </template>
        </van-tabbar-item>
        <van-tabbar-item @click="toMarket()">
          <span>{{$t('common.shichang')}}</span>
          <template #icon="props">
            <img :src="props.active ? require('./assets/image/market1.png'): require('./assets/image/market2.png')" />
          </template>
        </van-tabbar-item>
        <van-tabbar-item @click="toTransaction()">
          <span>{{$t('common.jiaoyi')}}</span>
          <template #icon="props">
            <img :src="props.active ? require('./assets/image/transaction1.png') : require('./assets/image/transaction2.png')" />
          </template>
        </van-tabbar-item>
        <van-tabbar-item @click="toContract()">
          <span>{{$t('common.heyue')}}</span>
          <template #icon="props">
            <img :src="props.active ? require('./assets/image/contract1.png') : require('./assets/image/contract2.png')" />
          </template>
        </van-tabbar-item>
        <van-tabbar-item @click="toWallet()">
          <span>{{$t('common.zijin')}}</span>
          <template #icon="props">
            <img :src="props.active ? require('./assets/image/wallet1.png') : require('./assets/image/wallet2.png')" />
          </template>
        </van-tabbar-item>
        <!-- <van-tabbar-item @click="toPersonal()">
          <span>登录</span>
          <template #icon="props">
            <img :src="props.active ? require('./assets/image/wallet1.png') : require('./assets/image/wallet2.png')" />
          </template>
        </van-tabbar-item> -->
      </van-tabbar>
    <!-- <router-view/> -->
    <keep-alive>
      <router-view v-if="$route.meta.keepAlive"></router-view>
    </keep-alive>
    <router-view v-if="!$route.meta.keepAlive"></router-view>
  </div>
</template>

<style lang="scss">
  @import "../src/common/css/base.css";
  @import "../src/assets/font/iconfont.css";
  @import "../src/assets/font/111/iconfont.css";
  body {
    font-size: 20px;
  }
  #app {
    font-family: Avenir, Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    color: #2c3e50;
  }
  .van-tabbar--fixed{
    background: #202641 !important;
    // background: #1E2B4D !important;
  }
  .van-hairline--top-bottom::after, .van-hairline-unset--top-bottom::after {
    border-width: 0.02rem 0 !important;
    border-top: 0rem solid #000 !important;
  }
  #nav {
    padding: 30px;
    a {
      font-weight: bold;
      color: #2c3e50;

      &.router-link-exact-active {
        color: #42b983;
      }
    }
  }
  html,body,#app{
    height: 100%;
  }
</style>

<script>
import Vue from 'vue'
import rem from '@/utils/rem'
import { Tabbar, TabbarItem } from 'vant'
Vue.use(Tabbar).use(TabbarItem)

export default {
  data () {
    return {
      active: 0,
      ajax: null,
      util: null,
      isIndex: 0
    }
  },
  methods: {
    toHome () {
      console.log(this.active, 'active')
      if (this.active === this.isIndex) { return }
      this.$router.push({
        path: '/',
        query: {
          // id: '视频',
          // showPopup: 'false'
        }
      })
      this.isIndex = this.active
    },
    toMarket () {
      console.log(this.active, 'active')
      if (this.active === this.isIndex) { return }
      this.$router.push({
        path: '/marketIndex',
        query: {
          // id: '视频',
          // showPopup: 'false'
        }
      })
      this.isIndex = this.active
    },
    toTransaction () {
      if (this.active === this.isIndex) { return }
      this.$router.push({
        path: '/transaction',
        query: {
          // id: '视频',
          // showPopup: 'false'
        }
      })
      this.isIndex = this.active
    },
    toContract () {
      if (this.active === this.isIndex) { return }
      this.$router.push({
        path: '/contract1',
        query: {
          // id: '视频',
          // showPopup: 'false'
        }
      })
      this.isIndex = this.active
    },
    toWallet () {
      if (this.active === this.isIndex) { return }
      this.$router.push({
        path: '/capitalIndex',
        query: {
          // id: '视频',
          // showPopup: 'false'
        }
      })
      this.active = 1
    },
    toPersonal () {
      if (this.active === this.isIndex) { return }
      this.$router.push({
        path: '/login',
        query: {
          // id: '视频',
          // showPopup: 'false'
        }
      })
      this.isIndex = this.active
    },
    toPersonal1 () {
      if (this.active === this.isIndex) { return }
      this.$router.push({
        path: '/register',
        query: {
          // id: '视频',
          // showPopup: 'false'
        }
      })
      this.isIndex = this.active
    }
  },
  watch: {
    $route: {
      handler (val, oldVal) {
        console.log(this.$route.path === '/market')
        if (this.$route.path === '/market') {
          this.isIndex = 1
          this.active = 1
        } else if (this.$route.path === '/home') {
          this.isIndex = 0
          this.active = 0
        } else if (this.$route.path === '/transaction') {
          this.isIndex = 2
          this.active = 2
        } else if (this.$route.path === '/contract1' || this.$route.path === '/contract/contract2' || this.$route.path === '/contract/contract3') {
          this.isIndex = 3
          this.active = 3
        } else if (this.$route.path === '/capital') {
          this.isIndex = 4
          this.active = 4
        }
      },
      // 深度观察监听
      deep: true
    }
  },
  mounted () {
    rem(750, 750)
  }
}
</script>
