import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import './permission'
import 'vant/lib/index.css'
import global_ from './components/Global'
import echarts from 'echarts'
import VueI18n from 'vue-i18n'
Vue.prototype.$echarts = echarts
Vue.prototype.GLOBAL = global_
Vue.config.productionTip = false
// *******************************************************************************************
Vue.use(VueI18n)
window.localStorage.setItem('language', 'zh')
const i18n = new VueI18n({

  // locale: 'en', // 语言标识 //this.$i18n.locale // 通过切换locale的值来实现语言切换
  locale: localStorage.getItem('language') || 'zh',
  messages: {
    zh: require('../src/utils/zh.json'),
    en: require('../src/utils/en.json')
  }
})
// Vue.prototype.$getZh = () => {
//   // i18n.locale = 'zh'
//   const locale = localStorage.getItem('language') || 'zh'
//   const temp = locale === 'zh' ? 'en' : 'zh'
//   i18n.locale = temp
//   localStorage.language = temp
// }
// Vue.prototype.$getEn = () => {
//   // i18n.locale = 'en'
//   const locale = localStorage.getItem('language') || 'zh'
//   const temp = locale === 'zh' ? 'en' : 'zh'
//   i18n.locale = temp
//   localStorage.language = temp
// }
Vue.prototype.$changeLan = () => {
  // i18n.locale = 'zh'
  const locale = localStorage.getItem('language') || 'zh'
  const temp = locale === 'zh' ? 'en' : 'zh'
  i18n.locale = temp
  localStorage.language = temp
  // 保存全局语言到state.language中
  store.commit('setLanguage', { temp: temp })
}
// ********************************************************************
new Vue({
  router,
  store,
  i18n,
  render: h => h(App)
}).$mount('#app')
